<template>
  <label class="form__label">
    <slot></slot>
    <span class="form__value"> {{ title }} </span>
    <span class="form__error" v-if="error">
      {{ error }}
    </span>
  </label>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: false,
    },
    error: {
      type: String,
      required: false,
    },
  },
};
</script>
