import { mapActions, mapMutations } from "vuex";

export default {
  components: { CartIndicator },
  created() {
    const userAccesKey = localStorage.getItem("userAccesKey");
    if (userAccesKey) {
      this.updateUserAccesKey(userAccesKey);
    }
    this.loadCart();
  },
  methods: {
    ...mapActions(["loadCart"]),
    ...mapMutations(["updateUserAccesKey"]),
  },
};
