import axios from "axios";

const API_BASE_URL = "https://vue-study.skillbox.cc";

export const loadProducts = (
  page,
  limit,
  categoryId,
  minPrice,
  maxPrice,
  colorId
) => {
  return axios.get(API_BASE_URL + "/api/products", {
    params: {
      page: page,
      limit: limit,
      categoryId: categoryId,
      minPrice: minPrice,
      maxPrice: maxPrice,
      colorId: colorId,
    },
  });
};

export const loadCategories = () => {
  return axios.get(API_BASE_URL + "/api/productCategories");
};

export const loadColors = () => {
  return axios.get(API_BASE_URL + "/api/colors");
};

export const loadProduct = (id) => {
  return axios.get(API_BASE_URL + "/api/products/" + id);
};

export const sendOrder = (data, userAccessKey) => {
  return axios.post(
    API_BASE_URL + "/api/orders",
    {
      ...data,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

// store

export const loadOrderInfoFromApi = (id, userAccessKey) => {
  return axios.get(API_BASE_URL + "/api/orders/" + id, {
    params: {
      userAccessKey: userAccessKey,
    },
  });
};

export const loadCartFromApi = (userAccessKey) => {
  return axios.get(API_BASE_URL + "/api/baskets", {
    params: {
      userAccessKey: userAccessKey,
    },
  });
};

export const addProductToCartToApi = ({ productId, amount }, userAccessKey) => {
  return axios.post(
    API_BASE_URL + "/api/baskets/products",
    {
      productId: productId,
      quantity: amount,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

export const updateCartProductAmountToApi = (
  { productId, amount },
  userAccessKey
) => {
  return axios.put(
    API_BASE_URL + "/api/baskets/products",
    {
      productId: productId,
      quantity: amount,
    },
    {
      params: {
        userAccessKey: userAccessKey,
      },
    }
  );
};

export const deleteCartProductFromApi = (id, userAccessKey) => {
  return axios.delete(API_BASE_URL + "/api/baskets/products", {
    data: {
      productId: id,
    },
    params: {
      userAccessKey: userAccessKey,
    },
  });
};
