<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> {{ countProducts }} товара </span>
    </div>

    <div class="content__catalog">
      <product-filter
        v-model:price-from="filterPriceFrom"
        v-model:price-to="filterPriceTo"
        v-model:category-id="filterCategoryId"
        v-model:color="filterColor"
      ></product-filter>

      <section class="catalog">
        <div v-if="productsLoading" class="content__title preload-text">
          Загрузка товаров...
        </div>

        <div v-if="productsLoadingFailed" class="content__title preload-text">
          Произошла ошибка при загрузке товаров...
        </div>
        <button
          class="button button--primery"
          v-if="productsLoadingFailed"
          @click.prevent="getProducts"
        >
          Попробовать еще раз
        </button>
        <product-list :products="products"></product-list>

        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        ></BasePagination>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import { loadProducts } from "@/api";

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      page: 1,
      productsPerPage: 6,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products: function () {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return { ...product, image: product.image.file.url };
          })
        : [];
    },
    countProducts: function () {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    page() {
      this.getProducts();
    },
    filterPriceFrom() {
      this.getProducts();
      this.page = 1;
    },
    filterPriceTo() {
      this.getProducts();
      this.page = 1;
    },
    filterCategoryId() {
      this.getProducts();
      this.page = 1;
    },
    filterColor() {
      this.getProducts();
      this.page = 1;
    },
  },
  methods: {
    getProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      loadProducts(
        this.page,
        this.productsPerPage,
        this.filterCategoryId,
        this.filterPriceFrom,
        this.filterPriceTo,
        this.filterColor
      )
        .then((response) => (this.productsData = response.data))
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => (this.productsLoading = false));
    },
  },
  created() {
    this.getProducts();
  },
};
</script>

<style scoped>
.preload-text {
  margin: 50px auto;
  font-weight: bold;
  text-align: center;
}
</style>
